
.fa-icon {
  width: auto;
  height: 1em;
  /* or any other relative font sizes */
}
.del {
  color: #f66;
  font-size: 1em;
}
.eq {
  font-size: 1.2em;
  font-weight: bold;
}
.numpad {
  font-size: 1.8em;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  width: 8.37em;
  margin-left: auto;
  margin-right: auto;
  border: 0px solid #ccc;
  border-radius: 6px;
}
.numpad DIV {
    width: 1.8em;
    height: -8.2em;
    border: 2px solid #ccc;
    border-radius: 10px;
    margin: 2px;
    padding: 10px;
    line-height: 1;
    cursor: pointer;
}



